							<view v-for="(g, i) in editMode ? form.gallery : gallery" :key="'g' + i" class="g-wrapper">
								<image class="g" :src="g" mode="aspectFill" @tap="preview(i)" />
							<view v-if="editMode" class="g-delete" @tap="deletePhoto(i)">√ó</view>
						</view>
