</template>

<script setup>
import { ref, computed, reactive } from 'vue'
import { onLoad } from '@dcloudio/uni-app'
import { api } from '@/utils/api.js'

const pet = ref({})

onLoad(async (query) => {
	// ËÆæÁΩÆÂØºËà™Ê†èËÉåÊôØËâ≤‰∏éÈ°µÈù¢ËÉåÊôØÈ°∂ÈÉ®È¢úËâ≤‰∏ÄËá¥
	uni.setNavigationBarColor({ frontColor: '#000000', backgroundColor: '#fff1a8' })
    console.log('=== ÂÆ†Áâ©ËØ¶ÊÉÖÈ°µÂä†ËΩΩË∞ÉËØï‰ø°ÊÅØ ===');
    console.log('URLÂèÇÊï∞:', query);
    
    if (query?.pet) {
        try {
            const data = JSON.parse(decodeURIComponent(query.pet))
            console.log('Ëß£ÊûêÂêéÁöÑÂÆ†Áâ©Êï∞ÊçÆ:', data);
            console.log('ÂÆ†Áâ©Â§¥ÂÉèURL:', data.avatarUrl);
            Object.assign(pet.value, data)
            console.log('ËµãÂÄºÂêéÁöÑpet.value:', pet.value);
            
            // ÊµãËØïÂõæÁâáURLÊòØÂê¶ÂèØËÆøÈóÆ
            if (data.avatarUrl) {
                console.log('üîç ÊµãËØïÂõæÁâáURLÂèØËÆøÈóÆÊÄß...');
                
                // ÊµãËØïÂéüÂßãURL
                uni.request({
                    url: data.avatarUrl,
                    method: 'HEAD',
                    success: (testRes) => {
                        console.log('‚úÖ ÂéüÂßãÂõæÁâáURLÊµãËØïÊàêÂäü:', testRes.statusCode);
                    },
                    fail: (testErr) => {
                        console.error('‚ùå ÂéüÂßãÂõæÁâáURLÊµãËØïÂ§±Ë¥•:', testErr);
                    }
                });
                
                // ÊµãËØïAPIË∑ØÁî±
                const filename = data.avatarUrl.split('/').pop();
                const testUrl = `http://pet-api.zbinli.cn/api/test-image/${filename}`;
                console.log('üß™ ÊµãËØïAPIË∑ØÁî±:', testUrl);
                
                uni.request({
                    url: testUrl,
                    method: 'GET',
                    success: (apiRes) => {
                        console.log('‚úÖ APIË∑ØÁî±ÊµãËØïÊàêÂäü:', apiRes.statusCode);
                    },
                    fail: (apiErr) => {
                        console.error('‚ùå APIË∑ØÁî±ÊµãËØïÂ§±Ë¥•:', apiErr);
                    }
                });
            }
        } catch (e) { 
            console.error('Ëß£ÊûêÂÆ†Áâ©Êï∞ÊçÆÂ§±Ë¥•:', e);
        }
    }
    // ÊãâÂèñËØ•ÂÆ†Áâ©ÁöÑÂ™í‰ΩìÂõæÁâá
    if (pet.value?.id) {
        try {
            const res = await api.getMedia({ petId: pet.value.id })
            const mediaList = Array.isArray(res) ? res : (res.media || res.data || [])
            gallery.value = mediaList.map(m => m.url).filter(Boolean)
        } catch (err) {
            console.warn('Âä†ËΩΩÂÆ†Áâ©Áõ∏ÂÜåÂ§±Ë¥•', err)
        }
    }
})

const recent = ref([
	{ id: 'r1', time: '‰ªäÂ§© 08:00', type: 'ÂñÇÈ£ü', desc: 'Áå´Á≤Æ 60g' },
	{ id: 'r2', time: 'Êò®Â§© 21:10', type: 'Ê∏ÖÊ¥Å', desc: 'Èì≤Á†Ç' }
])

const vaccines = ref([])
const temperament = ref('')
const gallery = ref([])

// ÁºñËæëÊ®°Âºè
const editMode = ref(false)
const genders = ref(['Â•≥Áîü', 'Áî∑Áîü'])
const genderIndex = ref(1)
const vaccineOptions = ref(['Â∑≤Êé•ÁßçÁå´‰∏âËÅîÁñ´Ëãó', 'Â∑≤Êé•ÁßçÁãÇÁä¨Áñ´Ëãó'])
const form = reactive({ name: '', months: '', weight: '', gender: 'male', breed: '', color: '', neutered: false, birthday: '', startTogether: '', avatar: '', vaccines: [], temperament: '', gallery: [] })
const originalGallery = ref([]) // ‰øùÂ≠òÁºñËæëÂâçÁöÑÂéüÂßãÁÖßÁâá

function startEdit() {
	editMode.value = true
    Object.assign(form, { ...pet.value, vaccines: [...vaccines.value], temperament: temperament.value, avatarUrl: pet.value.avatarUrl, gallery: [...gallery.value] })
	originalGallery.value = [...gallery.value] // ‰øùÂ≠òÂéüÂßãÁÖßÁâá
	genderIndex.value = form.gender === 'male' ? 1 : 0
}
function cancelEdit() {
	editMode.value = false
	gallery.value = [...originalGallery.value] // ÊÅ¢Â§çÂéüÂßãÁÖßÁâá
}
async function saveEdit() {
    // ÂâçÁ´ØÊú¨Âú∞‰øùÂ≠òÂ±ïÁ§∫ÔºõÂ¶ÇÈúÄÂêåÊ≠•ÂêéÁ´ØÔºåÂèØË∞ÉÁî® api.updatePet
    pet.value = { ...pet.value, name: form.name, months: form.months, weight: form.weight, gender: form.gender, breed: form.breed, color: form.color, neutered: form.neutered, birthday: form.birthday, startTogether: form.startTogether, avatarUrl: form.avatarUrl || pet.value.avatarUrl }
    vaccines.value = [...form.vaccines]
    temperament.value = form.temperament
    gallery.value = [...form.gallery]
    editMode.value = false
}
function onGenderChange(e) { genderIndex.value = Number(e.detail.value || 0); form.gender = genderIndex.value === 1 ? 'male' : 'female' }
function onVaccinesChange(e) { form.vaccines = e.detail.value || [] }
function pickAvatar() { uni.chooseImage({ count: 1, sizeType: ['compressed'], success: res => { form.avatar = res.tempFilePaths[0] } }) }
function pickGallery() { uni.chooseImage({ count: 9, sizeType: ['compressed'], success: res => { form.gallery = form.gallery.concat(res.tempFilePaths) } }) }
function deletePhoto(index) {
	if (editMode.value) {
		uni.showModal({
			title: 'Á°ÆËÆ§Âà†Èô§',
			content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÂº†ÁÖßÁâáÂêóÔºü',
			confirmText: 'Âà†Èô§',
			cancelText: 'ÂèñÊ∂à',
			confirmColor: '#ff4757',
			success: (res) => {
				if (res.confirm) {
					form.gallery.splice(index, 1)
				}
			}
		})
	}
}

function preview(index) {
    const list = editMode.value ? form.gallery : gallery.value
    if (!Array.isArray(list) || list.length === 0) return
    uni.previewImage({
        current: index,
        urls: list
    })
}

async function deletePet() {
    uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: `Á°ÆÂÆöË¶ÅÂà†Èô§ÂÆ†Áâ©"${pet.value.name}"ÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§çÔºÅ`,
        confirmText: 'Âà†Èô§',
        cancelText: 'ÂèñÊ∂à',
        confirmColor: '#ff4757',
        success: async (res) => {
            if (!res.confirm) return
            try {
                await api.deletePet(pet.value.id)
                uni.showToast({ title: 'ÂÆ†Áâ©Â∑≤Âà†Èô§', icon: 'success' })
                // ÈÄöÁü•ÂÖ∂‰ªñÈ°µÈù¢Âà∑Êñ∞
                uni.$emit && uni.$emit('pets:changed')
                setTimeout(() => { uni.navigateBack() }, 600)
            } catch (e) {
                uni.showToast({ title: 'Âà†Èô§Â§±Ë¥•', icon: 'none' })
            }
        }
    })
}

const togetherDays = computed(() => {
	const start = new Date(pet.value.startTogether).getTime()
	const today = new Date().setHours(0, 0, 0, 0)
	return Math.max(1, Math.floor((today - start) / 86400000) + 1)
})

function goEdit() { uni.navigateTo({ url: '/pages/editPet/editPet' }) }
function goAlbum() { uni.navigateTo({ url: '/pages/album/album' }) }

// ÂõæÁâáÂä†ËΩΩ‰∫ã‰ª∂
function onAvatarLoad(e) {
    console.log('‚úÖ Â§¥ÂÉèÂõæÁâáÂä†ËΩΩÊàêÂäü:', e);
}

function onAvatarError(e) {
    console.error('‚ùå Â§¥ÂÉèÂõæÁâáÂä†ËΩΩÂ§±Ë¥•:', e);
    console.log('ÂΩìÂâçÂõæÁâáURL:', pet.value.avatarUrl);
}
</script>

